@import url('https://fonts.googleapis.com/css2?family=Clicker+Script&family=Poppins:wght@200;300;400;500;600;700&display=swap');


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  scroll-behavior: smooth;
  scroll-padding-top: 2rem;
  font-family: "Poppins", sans-serif;
}

:root {
  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;

  --bg-black-900: #f2f2fc;
  --bg-black-100: #fdf9ff;
  --bg-black-50: #e8dfec;
  --text-black-900: #302e4d;
  --text-black-700: #504e70;

  --black3: #020c1b;
  --black-lighter: #1c4b91;
  --black-light: #173d77;
  --pink: #da2f68;
  --orange: #f89e00;
  --gradient: linear-gradient(98.37deg, #f89e00 0.99%, #da2f68 100%);

  --skin-color: #ec1839;
  --bg-color: #fff;
  --text-color: #0f0c27;
  --hover: hsl(260, 100%, 51%);
  --big-font: 3.2rem;
  --medium-font: 1.8rem;
  --p-font: 1rem;
  --main-color: #033a80;
  --color-primary-variant : rgba(77, 181, 255, 0.4);
  --color-bg-variant : #9cd3ee;
  --transition: all 400ms ease;
}

body.active {
  --text-color:#fff;
  --bg-color: #0f0c27
}


body {
 background: var(--bg-color);
 color: var(--text-color);

}

*::selection {
  background: var(--main-color);
  color: var(--bg-color);
}

section {
 padding: 50px 10%;
}

header {
  position: fixed;
  width: 100%;
  top: 0;
  right: 0;
  display: flex;
  z-index: 1000;
  align-items: center;
  justify-content: space-between;
  background: var(--bg-color);
  padding: 13px 10%;
  transition: 0.2s;
  box-shadow: -3px -3px 7px #ffffff73, 2px 2px 5px rgba(94, 104, 121, 0.288);

}

header.shadow { 
  box-shadow: 0 0 4px rgb(14 55 54/15%);
}

.logo {
  font-size: 1.6rem;
  font-weight: 600;
  color: var(--text-color);
}

.logo span {
  font-size: 1.6rem;
  font-weight: 600;
  color: crimson;
}

.navbar {
  display: flex;
}

.navbar.active {
  top: 100%;
}

.navbar a {
font-size: 1.1rem;
padding: 10px 20px;
color: var(--text-color);
font-weight: 500;
}

.navbar div {
  margin-left: 20px;
}

.navbar a:hover {
  color: var(--hover);
}

#menu-icon {
  font-size: 1.5rem;
  cursor: pointer;
  z-index: 10001;
  display: none;
}

#darkmode {
  padding-top: 5px;
  padding-left: 5px;
  font-size: 1.4rem;
  cursor: pointer;
}

.home {
  position: relative;
  width: 100%;
  min-height: 80vh;
  display: grid;
  /* margin-top: 2rem; */
  grid-template-columns: 0.2fr 1fr 1fr;
  gap: 1rem;
  align-items: center;
}

.home1-img {
  order: 3;
}

.home1-img img {
  width: 89%;
  border-radius: 50%;
  padding-top: 3rem;
  margin-left: 8rem;
  
}

.contratetext {
  text-align: center;
  background:  url(../src/assets/pic6.png);
  background-size: cover;
  position: relative;
  z-index: 1;
  padding: 80px 35px;
  width: 100%;
  background-blend-mode: color-burn;
}

.aboutstatus {
  text-align: center;
  background:  url(../src/assets/pic3.png);
  background-size: cover;
  position: relative;
  z-index: 1;
  padding: 12px 45px;
  width: 100%;
  background-blend-mode: color-burn;
}

.home-text span {
  font-size: var(--medium-font);
  font-weight: 500;
}

.home-text h1 {
  font-size: var(--big-font);
}

.home-text h2 {
  font-size: 1rem;
  font-weight: 400;
}

.home-text p {
  font-size: var(--p-font);
  font-weight: 400;
  margin: 0.7rem 0 1rem;
}

.social {
  display: flex;
  flex-direction: column;
}

.social a {
  margin-bottom: 1rem;
  font-size: 22px;
  color: var(--text-color);
}

.social a:hover {
  color: var(--hover);
}

.btn-bg {
  display: inline-block;
  background: var(--main-color);
  color: #fff;
  padding: 0.7rem 1.3rem;
  border-radius: 0.6rem;
}

.btn-bg:hover {
  background: var(--hover);
}

.heading {
  text-align: center;
}

.heading h2{
font-size: 2rem;
}

.heading span {
  font-size: var(--p-font);
  color: rgba(2, 166, 70);
} 

.about-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  margin-top: 0rem;
  align-items: center;
}

.about-img img {
width: 80%;
border-radius: 0.7rem;
}

.about-text {
  font-size: var(--p-font);
  font-weight: 400;
  text-align: left;
  margin-top: 3rem;
}

.information {
  margin: 1rem 0 1.4rem;
}

.information .info-box {
  display: flex;
  align-items: center;
  margin-bottom: 1.3rem;
}

.information .info-box span {
  font-weight: 400;
  margin-left: 1rem;
}

.container {
  max-width: 1100px;
  width: 100%;
  margin: auto;
  /* background: red; */
}


.experience__container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  margin-top: 4rem;
}

.experience__container > div {
  background: var(--color-bg-variant);
  padding: 2.4rem 2rem; 
  border-radius: 2rem;
  border: 1px solid transparent;
  transition: var(--transition);
  box-shadow: 0px 2px 7px rgba(14 55 54/15%);
}

.experience__container > div:hover {
  background: var(--color-primary-variant);
  /* border-color: var(--color-primary-variant); */
  cursor: default;
}

.experience__container > div h3 {
  text-align: center;
  margin-bottom: 2rem;
  color: var(--color-primary);
}

.experience__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 2rem;
}

.experience__details {
  display: flex;
  gap: 1rem;
}

.experience__details-icon {
  margin-top: 6px;
  color: var(--main-color);
}

.services-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, auto));
  justify-content: center;
  gap: 1rem;
  margin-top: 2rem;
}

.services-box {
  padding: 20px;
  width: 260px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  border-radius: 0.5rem;
  border-bottom: 2px solid var(--main-color);
  box-shadow: 0px 2px 7px rgba(14 55 54/15%);
}

.services-box:hover {
  background: var(--color-bg-variant);
}

.services-box h3 {
  font-size: 1rem;
  font-weight: 600;
  margin: 0.7rem 0 0.4rem;
}

.services-box .bx {
  padding-top: 2rem;
  font-size: 54px;
  color: var(--main-color);
}

.services-box:hover .bx {
  color: #22cf91;
}

.services-box a {
  color: var(--main-color);
  font-size: var(--p-font);
  font-weight: 500;
}

.services-box a:hover {
  color: #8b8a8f;
}

.contact-form {
  display: grid;
  place-items: center;
  margin-top: 2rem;
}

.contact-form form {
  display: flex;
  flex-direction: column;
  width: 650px;
}

form, input, textarea, button {
padding: 15px;
border-radius: 0.5rem;
width: 100%;
outline: none;
border: none;
margin-bottom: 1rem;
background: hsl(260, 100%, 44%, 0.1);
color: var(--text-color);
}

form input::placeholder , textarea::placeholder, button::placeholder {
  color: var(--text-color);
}

form textarea {
  resize: none;
  height: 200px;
}

.contact-button {
  width: 160px;
  cursor: pointer;
  background: #2768d9;
  color: #fff;
  justify-content: center;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-left: 35%;
}

.contact-button:hover {
  background: var(--hover);
}

.footer {
  display: grid;
  place-items: center;
  padding: 20px;
  color: #000;
}

.footer h2 {
  font-size: 1.5rem;
  margin-bottom: 1.6rem;
  color: var(--text-color);
}

.footer-social a i {
  color: var(--main-color);
  font-size: 1.5rem;
  margin-left: 10px;
  text-align: center;
  line-height: 40px;
  background: #fff;
  border-radius: 50%;
  width: 40px;
  height: 40px;
}

.copyright {

  padding: 20px;
  text-align: center;
  background: var(--main-color);
  color: #fff;
}

.copyright a {
  color: #22cf91;
}



@media (max-width:991px) {
  header {
    padding: 18px 4%;
  }

  section {
    padding: 50px 4%;
  }
}

@media (max-width:881px) {
:root {
  --big-font: 2.7rem;
  --medium-font: 1.4rem;
}
}

@media (max-width:768px) {
  :root {
    --big-font: 2.4rem;
    --medium-font: 1.2rem;
  }
  header {
    padding: 11px 4%;
  }
  #menu-icon {
    display: initial;
    color: var(--text-color);
  }
  header .navbar {
    position: absolute;
    left: 0;
    right: 0;
    top: -550px;
    display: flex;
    flex-direction: column;
    background: var(--bg-color);
    box-shadow: 0 4px 4px rgba(0 0 0/100%);
    transition: 0.2s ease;
    text-align: center;
  }
  .navbar .active {
    top: 100%;
  }
  .navbar a {
    padding: 1.5rem;
    display: block;
    background: var(--bg-color);
  }
  #darkmode {
    position: absolute;
    top: 1.4rem;
    right: 2rem;
  }
  .scroll-down {
    display: none;
  }
  .home {
    grid-template-columns: 0.5fr 3fr;
  }
  .home-text {
     grid-column: 1/3;
     padding: 1.4rem;
  }
  .home1-img {
    order: initial;
  }
  .about-container {
    grid-template-columns: 1fr;
  }
  .about-img {
    display: flex;
    justify-content: center;
  }
  .experience__container {
    grid-template-columns: 1fr;
  }
  .contact-form form {
    width: 300px;
  }
  .contact-button {
    margin-left: 25%;
  }
  }

  @media (max-width:340px) {
    :root {
      --big-font: 1.7rem;
      --medium-font: 1.1rem;
    }
    .home-text span {
      font-size: 1rem;
    }
    .home-text h2 {
      font-size: 0.9rem;
      font-weight: 500;
    }
    .information .info-box span {
      font-size: 1rem;
    }
    .contact-form form {
      width: 250px;
    }
    .contact-button {
      width: 20%;
    }
  }

  ::-webkit-scrollbar {
    display: none;
  }